@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,600;1,400;1,500&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: calibri;
  background-color: #111;
}

header {
  width: 100%;
  height: 120px;
  text-align: center;
}

a {
  text-decoration: none;
  color: #fff;
}

.input {
  margin-top: 50px;
  border-bottom: 1px solid #555;
  border-left: none;
  border-right: none;
  border-top: none;
  width: 15%;
  background-color: transparent;
  color: #fff;
  font-family: "Playfair Display", serif;
  font-size: 20px;
}

.button {
  width: 100px;
  padding: 5px;
  border: none;
  background-color: transparent;
  color: #fff;
  font-size: 1.5rem;
  letter-spacing: 1.4px;
  font-family: "Playfair Display", serif;
  width: 120px;
}

.noentries {
  color: #fff;
  text-align: center;
  font-family: "Playfair Display", serif;
  font-style: italic;
}

.main-container {
  vertical-align: top;
  position: relative;
  width: 95%;
  margin-left: 10%;
}

.item-holder {
  vertical-align: top;
  width: 20%;
  height: 500px;
  border-radius: 10px;
  top: 55px;
  box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.4);
  color: #fff;
  margin: 1%;
  display: inline-block;
}

.item-image {
  width: 100%;
  height: 250px;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

.item-content {
  padding: 20px;
  font-family: "Playfair Display", serif;
}

.descriptive-h3 {
  text-transform: uppercase;
  font-family: "Playfair Display", serif;
}

.read-more-button {
  padding: 12px;
  width: 100px;
  border: 1px solid #bbb;
  background-color: transparent;
  border-radius: 30px;
  color: #fff;
}

div button.read-more-button:hover {
  background-color: #444;
  cursor: pointer;
  border: transparent;
}

.descriptive-text {
  line-height: 26px;
  font-family: "Playfair Display", serif;
  font-weight: 100;
}

/*when you click read more */

.item-detail {
  color: #fff;
  width: 80%;
  text-align: center;
  margin-top: 5%;
  padding: 20px;
}

.item-detail-image-holder {
  display: inline-block;
  width: 40%;
  margin-left: 0;
}

.item-detail-image {
  width: 100%;
  height: 250px;
  border-radius: 5px;
  box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.8);
}

.item-detail-text {
  vertical-align: top;
  padding: 50px 10px 10px;
  text-align: left;
  width: 40%;
  margin-left: 3%;
  display: inline-block;
  line-height: 30px;
}

.item-detail-descriptive-heading {
  font-family: "Playfair Display", serif;
  font-weight: 600;
  font-size: 30px;
  letter-spacing: 5px;
}

.item-detail-descriptive-text {
  font-family: "Playfair Display", serif;
  font-weight: 100;
  letter-spacing: 0.5px;
  padding-bottom: 20px;
}

.item-detail-go-back-button {
  border: 1px solid #fff;
  border-radius: 30px;
  padding: 10px;
  width: 120px;
  background-color: transparent;
  color: #fff;
}

.item-detail-go-back-button:hover {
  background-color: #444;
  cursor: pointer;
  border: transparent;
}

.keyword-holder {
  padding-bottom: 20px;
}

.keyword {
  padding: 0 20px;
  border-left: 1px solid #fff;
}

.container-div {
  position: relative;
  width: 90%;
}

.notfoundimage {
  width: 60%;
  margin-left: 26%;
  box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.4);
  border-radius: 20px;
  margin-top: 2%;
  text-align: center;
}

.notfound {
  text-align: center;
  font-size: 30px;
  font-family: "Playfair Display", serif;
  color: #ddd;
  font-size: 60px;
  display: inline-block;
  position: absolute;
  left: 28%;
  top: 50%;
}

@media only screen and (max-width: 1400px) {
  .input {
    width: 40%;
  }

  .item-holder {
    width: 40%;
  }

  .item-detail-image-holder {
    width: 50%;
    margin-right: 20%;
  }

  .item-detail-text {
    width: 80%;
    margin-left: 10%;
    padding: 50px;
    display: block;
  }
}

@media only screen and (max-width: 800px) {
  .main-container {
    width: 90%;
    margin-left: 5%;
  }

  .item-holder {
    width: 80%;
    margin-left: 15%;
    margin-top: 4%;
  }

  .item-detail-image-holder {
    width: 100%;
    margin-left: 1%;
    display: block;
  }

  .item-detail-text {
    width: 100%;
    margin-left: 1%;
    padding: 40px 0;
    display: block;
  }
}

